body {
  line-height: 1.5;
  color: #1F272E;
}

a {
  color: #2D95F0;
}

p {
  margin: 1em 0 !important;
}

.with-container p {
  margin: 5px 0 !important;
}

.ql-editor {
  white-space: normal;
}
.ql-editor p {
  margin: 0 !important;
}

hr {
  border-top: 1px solid #F4F5F6;
}

.body-table {
  border-collapse: collapse;
  table-layout: fixed;
  margin: 0 auto;
  border-spacing: 0;
  height: 100% !important;
  width: 100% !important;
  font-weight: normal;
  font-family: "-apple-system", "BlinkMacSystemFont", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
  line-height: 1.4;
}
.body-table .brand-logo {
  margin: auto;
  text-align: center;
  border: 0;
  outline: none;
  text-decoration: none;
  max-height: 40px;
  width: auto;
  margin-bottom: 10px;
}
.body-table td {
  font-family: "-apple-system", "BlinkMacSystemFont", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}
.body-table .body-content {
  padding: 20px 0;
}

.email-header, .email-body, .email-footer {
  width: 100% !important;
  min-width: 100% !important;
}

.email-body {
  font-size: 14px;
  color: #1F272E;
}
.email-body .gray-container {
  background-color: #F9FAFA;
  border-radius: 8px;
  padding: 25px 15px;
}

.email-footer {
  font-size: 12px;
  line-height: 20px;
}

.email-header .brand-image {
  width: 24px;
  height: 24px;
  display: block;
}

.email-header-title {
  margin-top: 20px !important;
  font-size: 20px;
  font-weight: 600;
}

.body-table.with-container {
  background-color: #F4F5F6;
  color: #1F272E;
}
.body-table.with-container .body-content {
  padding: 60px 40px;
}
.body-table.with-container .email-container {
  max-width: 600px;
  border-spacing: 0;
  padding: 30px;
  border-radius: 8px;
  overflow: hidden;
  background-color: white;
}
.body-table.with-container .email-container .email-body {
  border-radius: 0 0 4px 4px;
  border-top: none;
}
.body-table.with-container .email-container .email-body tr {
  border-collapse: collapse;
  border-bottom: none;
}
.body-table.with-container .email-footer {
  border-top: none;
}

@media only screen and (max-width: 700px) {
  .body-table.with-container .body-content {
    padding: 30px 20px;
  }
  .body-table.with-container .email-container {
    padding: 20px;
    width: auto;
  }
}
.email-footer-container {
  margin-top: 40px;
}
.email-footer-container > div:not(:last-child) {
  margin-bottom: 5px;
}

.email-unsubscribe a {
  color: #687178;
  text-decoration: underline;
}

.btn {
  text-decoration: none;
  padding: 4px 20px;
  font-size: 13px;
  border: 1px solid;
  border-radius: 6px;
  color: #fff;
  background-color: #98A1A9;
  border-color: transparent;
  display: inline-block;
  line-height: 20px;
}
.btn.btn-primary {
  color: #fff;
  background-color: #2490EF;
}

.table {
  width: 100%;
  border-collapse: collapse;
}
.table td, .table th {
  padding: 8px;
  line-height: 1.42857143;
  vertical-align: top;
  border-top: 1px solid #F4F5F6;
  text-align: right;
}
.table th {
  font-weight: bold;
}
.table > thead > tr > th {
  vertical-align: middle;
  border-bottom: 2px solid #F4F5F6;
}
.table > thead:first-child > tr:first-child > th {
  border-top: none;
}
.table.table-bordered {
  border: 1px solid #F4F5F6;
}
.table.table-bordered td, .table.table-bordered th {
  border: 1px solid #F4F5F6;
}

.more-info {
  font-size: 80% !important;
  color: #687178 !important;
  padding-top: 10px;
}

.text-right {
  text-align: left !important;
}

.text-center {
  text-align: center !important;
}

.text-muted {
  color: #687178 !important;
}

.text-extra-muted {
  color: #687178 !important;
}

.text-small {
  font-size: 12px;
}

.text-medium {
  font-size: 13px;
}

.text-regular {
  font-size: 14px;
}

.text-lg {
  font-size: 16px;
}

.text-xl {
  font-size: 18px;
}

.text-2xl {
  font-size: 20px;
}

.text-bold {
  font-weight: bold;
}

.screenshot {
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1);
  border: 1px solid #F4F5F6;
  margin: 8px 0;
  max-width: 100%;
}

.list-unstyled {
  list-style-type: none;
  padding: 0;
}

/* auto email report */
.report-title {
  margin-bottom: 20px;
}

/* csslint ignore:end */
.avatar {
  display: inline-block;
  vertical-align: middle;
}

.avatar-frame {
  display: inline-block;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center center;
  border-radius: 50%;
  background-color: #FFEAE1;
  font-size: 12px;
}

.avatar img {
  max-width: 100%;
  max-height: 100%;
  border-radius: 50%;
}

.avatar .standard-image {
  border-radius: 50%;
  border: none;
}

.standard-image {
  background-color: #FFEAE1;
  color: #CB5A2A;
  font-weight: normal;
  width: 40px;
  height: 40px;
  text-align: center;
  vertical-align: middle;
  line-height: 40px;
  font-size: 12px;
}

blockquote {
  padding: 1px 15px;
  position: relative;
  margin-right: 0px;
  border-right: 2px solid #DCE0E3;
}
blockquote p {
  margin: 5px 0;
}